.ma-title, .ma-menu__link, .ma-banner__description, .ma-team-card__title, .ma-team-card__subtitle, .ma-team-card__description, .ma-team__header, .ma-plan__title, .ma-plan__content-item, .ma-plan__content-item--large, .ma-contact-information__title, .ma-contact-information__description, .ma-contact-information__phone, .ma-contact-form__btn, input::-webkit-input-placeholder,
